//CSS
import '../css/AddContent.css';
import "katex/dist/katex.css";
import 'markdown-it-highlight/dist/index.css';

//IMPORT
import React from 'react';
import MdEditor from 'react-markdown-editor-lite';
import MarkdownIt from 'markdown-it';
import markdownItHighlight from 'markdown-it-highlight';
import MarkdownItTexmath from 'markdown-it-texmath';
import katex from 'katex';


import { Select, Form, Input, Button } from 'antd';
const { Option } = Select;

const categoryController = require('../controller/Category')();
const categories = categoryController.getCategories();

class AddContent extends React.Component {
  mdParser = null;
  constructor(props) {
    super(props);
    this.mdParser = new MarkdownIt({
      html: true,
      linkify: true,
      typographer: true    
    })
    .use(markdownItHighlight)
    .use(MarkdownItTexmath.use(katex));
    this.state = {
      subject: '',
      content: '' 
    };
    this.onChangeSubject = this.onChangeSubject.bind(this);
  }
  handleEditorChange ({html, text}) {    
    console.log('handleEditorChange', html, text)
  }
   
  onChangeSubject(value) {
    console.log(value);
  }

  onSearch(val) {
    console.log('search:', val);
  }

  render() {
    return (      
      <div className="addcontent-div-backgroud">
         <div className="addcontent-div-main">
            <h1 className="addcontent-title">Criar Conteúdo Novo</h1> 
            <Form.Item label="Selecione a Categoria">
               <Select
                  showSearch
                  className="addcontent-select"
                  placeholder="Selecione Um Assunto"
                  optionFilterProp="children"
                  filterOption = { (input, option) => option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
                  >{
                     categories.map(function(item, key){
                        return <Option key={key} value={item.key}>{item.title}</Option>
                     })            
                  }
               </Select>
            </Form.Item>
            <Form.Item label="Nome do Assunto">
               <Input               
                  placeholder="Nome do Assunto"
               />        
            </Form.Item> 
            <div className="addcontent-div-mdeditor">       
               <MdEditor
                  value={this.state.content}
                  renderHTML={(text) => this.mdParser.render(text)}
                  onChange={this.handleEditorChange} 
               />  
            </div>
            <div className="addcontent-div-button" >
              <Button type="primary" shape="round" icon="save" size="large">Salvar</Button>
            </div>
         </div>
      </div>
    )
  }
}

export default AddContent;