//CSS
import '../css/ProblemsList.css';
//IMPORT
import React from 'react';

import { Table, Input, Button, Icon } from 'antd';
import {Tag } from 'antd';
import { Select } from 'antd';
import Highlighter from 'react-highlight-words';
const { Option } = Select;

const data = [
  {
    key: '1',
    name: 'Medalhas',
    tags: ['Introdução'],
    difficulty: 10,
    link: 'https://neps.academy/problem/2'
  },
  {
    key: '2',
    name: 'Gincana',
    tags: ['Introdução'],
    difficulty: 30,
    link: 'https://neps.academy/problem/3'
  },
  {
    key: '3',
    name: 'Caminhos do Reino',
    tags: ['Algoritmos em Grafos', 'Programação Dinâmica'],
    difficulty: 90,
    link: 'https://neps.academy/problem/7'
  }
];

class ProblemsList extends React.Component{
  state = {
    searchText: '',
  };

  getColumnSearchProps = dataIndex => ({
    filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
      <div style={{ padding: 8 }}>
        <Input
          ref={node => {
            this.searchInput = node;
          }}
          placeholder='Buscar pelo Nome'
          value={selectedKeys[0]}
          onChange={e => setSelectedKeys(e.target.value ? [e.target.value] : [])}
          onPressEnter={() => this.handleSearch(selectedKeys, confirm)}
          style={{ width: 188, marginBottom: 8, display: 'block' }}
        />
        <Button
          type="primary"
          onClick={() => this.handleSearch(selectedKeys, confirm)}
          icon="search"
          size="small"
          style={{ width: 90, marginRight: 8 }}
        >
          Buscar
        </Button>
        <Button onClick={() => this.handleReset(clearFilters)} size="small" style={{ width: 90 }}>
          Resetar
        </Button>
      </div>
    ),
    filterIcon: filtered => (
      <Icon type="search" style={{ color: filtered ? '#1890ff' : undefined }} />
    ),
    onFilter: (value, record) =>
      record[dataIndex]
        .toString()
        .toLowerCase()
        .includes(value.toLowerCase()),
    onFilterDropdownVisibleChange: visible => {
      if (visible) {
        setTimeout(() => this.searchInput.select());
      }
    },
    render: text => (
      <Highlighter
        highlightStyle={{ backgroundColor: '#ffc069', padding: 0 }}
        searchWords={[this.state.searchText]}
        autoEscape
        textToHighlight={text.toString()}
      />
    ),
  });


  handleSearch = (selectedKeys, confirm) => {
    confirm();
    this.setState({ searchText: selectedKeys[0] });
  };

  handleReset = clearFilters => {
    clearFilters();
    this.setState({ searchText: '' });
  };
  render() {
    const columns = [
      {
        title: 'Nome',
        dataIndex: 'name',
        key: 'name',
        width: '30%',
        ...this.getColumnSearchProps('name')
      },
      {
        title: 'Assuntos',
        dataIndex: 'tags',
        key: 'tags',
        width: '30%',
        render: tags => (
          <span>
            {tags.map( (tag, idx) => {
              const colors = ['blue', 'purple', 'magenta', 'red', 'orange', 'green'];               
              return (
                <Tag className="problemslist-tag" color={colors[idx%colors.length]} key={tag}>
                  {tag.toUpperCase()}
                </Tag>
              );
            })}
          </span>
        )      
      },
      {
        title: 'Dificuldade (0-100)',
        dataIndex: 'difficulty',
        key: 'difficulty',
        defaultSortOrder: 'ascend',
        sorter: (a, b) => a.difficulty - b.difficulty
      },
      {
        title: 'Link',
        dataIndex: 'link',
        key: 'link',
        render: text => <a  rel="noopener noreferrer" href={text} target="_blank"><Button type="primary" ghost>Praticar</Button></a>
      }
    ];

    return (
      <div className="problemslist-div-backgroud">
        <div className="problemslist-div-main">
          <h1 className="problemslist-title">Lista de Questões</h1> 
          <div className="problemslist-div-select">       
            <Select
              className="problemslist-select"
              mode="multiple"
              showSearch
              placeholder="Selecione um assunto"
              optionFilterProp="children"
              filterOption={(input, option) =>
                option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
              }
            >
              <Option value="Introdução">Introdução</Option>
              <Option value="grafos">Algoritmos em Grafos</Option>
              <Option value="programacao_dinamica">Programação Dinâmica</Option>
            </Select>
          </div>
          <Table columns={columns} dataSource={data} scroll={{x: 200}} />
        </div>
      </div>
    );
  }
} 

export default ProblemsList;