module.exports = function() {
   let controller = {};

   controller.getCategories = function(){
      return [{id: 0, key: 'introducao', title: 'Introdução ao C++', img: process.env.PUBLIC_URL+'/img/introduction.png'}, 
      {id: 1, key: 'matematica', title: 'Matemática Computacional', img: process.env.PUBLIC_URL+ '/img/math.png'}, 
      {id: 2, key: 'estrutura-de-dados', title: 'Estrutura de Dados', img: process.env.PUBLIC_URL + '/img/data_structure.png'}, 
      {id: 3, key: 'tecnicas-de-programacao', title: 'Técnicas de Programação', img: process.env.PUBLIC_URL + '/img/technique.png'}, 
      {id: 4, key: 'string', title: 'Processamento de String', img: process.env.PUBLIC_URL + '/img/string.png'}, 
      {id: 5, key: 'geometria', title: 'Geometria Computacional', img:process.env.PUBLIC_URL + '/img/geometry.png'}, 
      {id: 6, key: 'grafos', title: 'Algoritmos em Grafos', img: process.env.PUBLIC_URL + '/img/graph.png'}, 
      {id: 7, key: 'variados', title: 'Variados', img: process.env.PUBLIC_URL + '/img/miscellaneous.png'}];
   }

   controller.getCategory = function(key){
      const categories = controller.getCategories();

      for(const category of categories) {
         if(category.key === key)
            return category;
      }
      return {};
   }
   return controller;
};