//CSS
import '../css/ViewContent.css';
//IMPORT
import React from "react";
import MarkdownIt from 'markdown-it';
import markdownItHighlight from 'markdown-it-highlight';
import MarkdownItTexmath from 'markdown-it-texmath';
import katex from 'katex';
import dompurify from 'dompurify';
import { Link } from 'react-router-dom'
import { Breadcrumb } from 'antd';


const categoryController = require('../controller/Category')();
const subjectController = require('../controller/Subject')();
const contentController = require('../controller/Content')();


function MarkdownWithLatexAndCode(props){
  const sanitizer = dompurify.sanitize;
  
  let mdParser = new MarkdownIt({
    html: true,
    linkify: true,
    typographer: true    
  })
  .use(markdownItHighlight)
  .use(MarkdownItTexmath.use(katex));

  return <div dangerouslySetInnerHTML={{__html: sanitizer(mdParser.render(props.value))}}/>;
}


function ViewContent(props){
  const category = categoryController.getCategory(props.match.params.category);
  const subject = subjectController.getSubject(category.key, props.match.params.subject);
  const text = contentController.getText(subject.key);

  //console.log(props.match.params);
  return (
    <div className="viewcontent-div-main">    
      <div className="viewcontent-div-content">
        <Breadcrumb>
          <Breadcrumb.Item>
            <Link to="/">Início</Link>
          </Breadcrumb.Item>
          <Breadcrumb.Item>
            <Link to="/conteudo">Conteúdo</Link>
          </Breadcrumb.Item>
          <Breadcrumb.Item>
            <Link to={'/conteudo/'+category.key}>{category.title}</Link>
          </Breadcrumb.Item>
          <Breadcrumb.Item>
            {subject.title}
          </Breadcrumb.Item>
        </Breadcrumb>      
        <MarkdownWithLatexAndCode value = {text}/>
      </div>
    </div>
  );
} 

export default ViewContent;