//CSS
import '../css/EditContent.css';
import "katex/dist/katex.css";
import 'markdown-it-highlight/dist/index.css';

//IMPORT
import React from 'react';
import MdEditor from 'react-markdown-editor-lite';
import MarkdownIt from 'markdown-it';
import markdownItHighlight from 'markdown-it-highlight';
import MarkdownItTexmath from 'markdown-it-texmath';
import katex from 'katex';

import { Select, Form, Button } from 'antd';
const { Option } = Select;

const categoryController = require('../controller/Category')();
const subjectController = require('../controller/Subject')();
const contentController = require('../controller/Content')();
const categories = categoryController.getCategories();

class EditContent extends React.Component {
  mdParser = null;
  constructor(props) {
    super(props);
    this.mdParser = new MarkdownIt({
      html: true,
      linkify: true,
      typographer: true    
    })
    .use(markdownItHighlight)
    .use(MarkdownItTexmath.use(katex));
    this.state = {
      content: '', 
      subjects: []
    };
    this.onChangeCategory = this.onChangeCategory.bind(this);
    this.onChangeSubject = this.onChangeSubject.bind(this);
  }
  handleEditorChange ({html, text}) {    
    console.log('handleEditorChange', html, text)
  }

  onChangeCategory(value) {
    var new_subjects = subjectController.getSubjects(value);
    this.setState({
      content: '', 
      subjects : new_subjects
    });
    console.log(`selected ${value}`);
  }

  onChangeSubject(value) {
    var new_text = contentController.getText(value);
    this.setState({
      content : new_text
    });
    console.log(`selected ${value}`);
  }

  onSearch(val) {
    console.log('search:', val);
  }

  render() {
    return ( 
      <div className="editcontent-div-backgroud">
         <div className="editcontent-div-main">
            <h1 className="editcontent-title">Editar Conteúdo</h1> 
            <Form.Item label="Selecione a Categoria">
            <Select
          showSearch
          className="editcontent-select"
          placeholder="Selecione Um Assunto"
          optionFilterProp="children"
          onChange={this.onChangeCategory}
          filterOption={(input, option) =>
            option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
          }
        >
          {
            categories.map(function(item, key){
              return <Option key={key} value={item.key}>{item.title}</Option>
            })            
          }
        </Select>
            </Form.Item>
            <Form.Item label="Selecione o Assunto">
               <Select
               showSearch
               className="editcontent-select"
               placeholder="Selecione o assunto"
               optionFilterProp="children"
               onChange={this.onChangeSubject}
               filterOption={(input, option) =>
                  option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
               }
            >
               {
                  this.state.subjects.map(function(item, key){
                  return <Option key={key} value={item.key}>{item.title}</Option>
                  })            
               }
            </Select>
            </Form.Item> 
            <div className="editcontent-div-mdeditor">       
               <MdEditor
                  value={this.state.content}
                  renderHTML={(text) => this.mdParser.render(text)}
                  onChange={this.handleEditorChange} 
               />  
            </div>
            <div className="editcontent-div-button" >
              <Button type="primary" shape="round" icon="save" size="large">Salvar</Button>
            </div>
         </div>
      </div>
    )
  }
}

export default EditContent;