//CSS
import '../css/Category.css';
//IMPORT
import React from 'react';
import Subjects from './Subjects';
import { Switch, Route, Link } from 'react-router-dom';


const categoryController = require('../controller/Category')();
const categories = categoryController.getCategories();

class Category extends React.Component{
    constructor(props){
      super(props);
      const category = categoryController.getCategory(props.match.params.subject);
      if(category)
        this.state = {selected: category.id};
      else
        this.state = {selected: 0};
    }

    onChangedSelected = (id) => {
      this.setState({selected: id});
    }

    styleSelected = (id) => {
      if(id === this.state.selected)
        return {'opacity': '1'};
      else  
        return {};
    }

    render(){
      var onChangedSelected = this.onChangedSelected;
      var styleSelected = this.styleSelected;
      return (      
        <div className="category-div-backgroud">
          <div className="category-div-main">
            <div>          
              <div className="category-div-menu">                                  
                {
                  categories.map(function(item){
                    return(
                      <Link to={"/conteudo/"+item.key} key={item.id}>
                        <div className="menu-item " style={styleSelected(item.id)}  onClick={() => onChangedSelected(item.id)}>
                          <img className="menu-img" src={item.img} alt={item.title}/>
                          <p>{item.title}</p>
                        </div>       
                      </Link>       
                    );
                  })
                }
              </div>
              <div className="category-div-content">
                <Switch>
                  {
                    categories.map(function(item){
                      return(
                        <Route key={item.key} path={"/conteudo/"+item.key} exact={true}>
                          <Subjects category={item}/>
                        </Route>
                      );
                    })
                  }
                </Switch>
              </div>
            </div>
          </div>
        </div>
      );
    }
} 

export default Category;