module.exports = function() {
   let controller = {};

   controller.getSubjects = function(key){
      switch(key){
         case 'introducao':
            return [{id: 0, key: 'introducao', title: 'Introdução ao C++'},
            {id: 1, key: 'entrada-saida', title: 'Entrada e Saída'},
            {id: 2, key: 'variaveis', title: 'Variáveis'},
            {id: 3, key: 'condicional', title: 'Condicional'},
            {id: 4, key: 'laco-de-repeticao', title: 'Laço de Repetição'},
            {id: 5, key: 'funcao', title: 'Função'},
            {id: 6, key: 'matematica', title: 'Biblioteca de Matemática'}]
         case 'matematica':
            return [{id: 0, key: 'primos', title: 'Primos'},
            {id: 1, key: 'divisibilidade', title: 'Divisibilidade'},
            {id: 2, key: 'euclides', title: 'Algoritmo de Euclides'},
            {id: 3, key: 'crivo', title: 'Crivo de Eratóstenes'},
            {id: 4, key: 'determinante', title: 'Determinante'},
            {id: 5, key: 'contagem', title: 'Princípio da Contagem'},
            {id: 6, key: 'fft', title: 'Transformada Rápida de Furier'}]
         default:
            return [];           
      }
   }

   controller.getSubject = function(key_category, key_subject){
      const subjects = controller.getSubjects(key_category);

      for(const subject of subjects) {
         if(subject.key === key_subject)
            return subject;
      }
      return {}
   }

   return controller;
};
