import '../css/SignUp.css';
//IMPORT
import React from 'react';
import { Form, Icon, Input, Button } from 'antd';
import { Select } from 'antd';

const { Option } = Select;

class SignUp extends React.Component {
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  };
  render() {
    return (
        <div className="signup-div-main">
            <Form onSubmit={this.handleSubmit} className="signup-form">
              <Form.Item>
                <div className="signup-icon">              
                  <Icon className="signup-title" type="user-add" /> 
                </div> 
                <h1 className="signup-title">Cadastrar</h1>
              </Form.Item>

              <Form.Item label="Nome">
                  <Input
                    prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
                  />        
              </Form.Item>

              <Form.Item label="E-mail">
                  <Input
                    prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
                  />        
              </Form.Item>
              <Form.Item label="Senha">
                  <Input.Password
                    prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
                  />
              </Form.Item>
              <Form.Item label="Confirmação da Senha">
                  <Input.Password
                    prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
                  />
              </Form.Item>
              <Form.Item label="Selecione a Sua Universidade">
                <Select
                  showSearch
                  placeholder="Escolha..."
                  optionFilterProp="children"
                  filterOption={(input, option) =>
                    option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                  }
                >
                  <Option value="ufc">UFC</Option>
                  <Option value="ifce">IFCE</Option>
                  <Option value="uece">UECE</Option>
                </Select>
              </Form.Item>
              <Form.Item>
                  <Button type="primary" htmlType="submit" className="signup-form-button">Cadastrar</Button>
              </Form.Item>
            </Form>            
        </div>
    );
  }
}

export default SignUp;