//CSS
import '../css/Subjects.css';
//IMPORT
import React from 'react';
import { Row, Col } from 'antd';
import { Link } from 'react-router-dom';
import { Card } from 'antd';
import { Button } from 'antd';
import { Icon } from 'antd';

const subjectController = require('../controller/Subject')();

function Subjects(props){
  const category = props.category;
  const subjects = subjectController.getSubjects(category.key);
  
  return(
    <Row type="flex"  align="top">
      {
        subjects.map(function(item, key){
          return (
            <Col key={key} order={key} xs={24} sm={12} md={8} lg={6} xl={6}>
              <Link to={"/conteudo/" + category.key +"/"+item.key} key={item.id}>
              <div className="subject-div-center">
                <Card
                  hoverable
                  className="subject-div-item"
                  cover={<div className="subject-icon" style={{backgroundImage: 'url('+category.img+')'}}></div>}>
                  <p className="subject-title">{item.title}</p>
                  <Button type="primary" ghost style={{ width: "100%"}}><Icon type="smile" /> Aprenda mais</Button>
                </Card>
              </div>
              </Link>
            </Col>  
          );
        })
      }
    </Row>
  );
}

export default Subjects;


