//CSS
import '../css/App.css';
//IMPORT
import Welcome from './Welcome';
import AddContent from './AddContent';
import EditContent from './EditContent';
import ViewContent from './ViewContent';
import Category from './Category';
import ProblemsList from './ProblemsList';
import About from './About';

import React from 'react';
import { Switch, Route, Link, Redirect} from 'react-router-dom'
import { Layout, Menu} from 'antd';
const { Header, Content, Footer} = Layout;

class App extends React.Component{
  constructor(props){
    super(props);
    this.state = {selected: ['0']};
  }
  cleanSelected = () => {
    this.setState({selected: []});
  }
  onChangedSelected = (e) => {
    this.setState({selected: [e.key]});
  }
  onClickLogo = () => {
    this.setState({selected: ['0']});
  }
  render(){
    return (
      <Layout>
          <Header>
              <Link to="/"><img className="app-logo" src={process.env.PUBLIC_URL+"/img/logo.png"} onClick={this.onClickLogo} alt="Logomarca do Site"/></Link>
              <Menu className="app-menu" theme="dark" mode="horizontal" selectedKeys={this.state.selected} onSelect={this.onChangedSelected}>
                <Menu.Item key="0"><Link to="/">Início</Link></Menu.Item>
                <Menu.Item key="1"><Link to="/conteudo">Conteúdo</Link></Menu.Item>
                <Menu.Item key="3"><Link to="/questoes">Questões</Link></Menu.Item>
                <Menu.Item key="4"><Link to="/sobre">Sobre</Link></Menu.Item>                
              </Menu>
          </Header>
          <Content className="app-content">        
            <Switch>
                <Route path="/" exact={true} component={Welcome} />
                <Route path="/conteudo/:category" exact={true} component={Category} />
                <Route path="/conteudo/:category/:subject"  exact={true} component={ViewContent} />
                <Route path="/questoes" exact={true} component={ProblemsList} />
                <Route path="/sobre" exact={true} component={About} />
                <Route path="/editar_conteudo" exact={true} component={EditContent} />
                <Route path="/adicionar_conteudo" exact={true} component={AddContent} />

                <Redirect from="/conteudo/" exact={true} to="/conteudo/introducao" />
                <Redirect from="/conteudo" exact={true} to="/conteudo/introducao" />
                <Redirect from="*" to="/" />
            </Switch>
          </Content>
          <Footer className="app-footer">Code Marathon ©2019 Criado por <a href="https://github.com/paulomiranda98" target="_blank" rel="noopener noreferrer">Paulo Miranda</a></Footer>
      </Layout>
    );
  }
}

export default App;