//CSS
import '../css/About.css';
//IMPORT
import React from 'react';
import { Timeline } from 'antd';

const collaboratorController = require('../controller/Collaborator')();
const collaborators = collaboratorController.getCollaborators();

function Collaborator(props){
  const collaborator = props.collaborator;
  return (
    <div>
      <p>{collaborator.date} - {collaborator.name} começou a contribuir com o site</p>
      <div className="about-collaborators">
        <img alt={"foto de " + collaborator.name} src={collaborator.img}/>
        <div>
          <h2>{collaborator.name}</h2>
          <h3>{collaborator.occupation}</h3>
          <p>{collaborator.about}</p>
        </div>
      </div>
    </div>
  );
}

class About extends React.Component{
  render() {
    return (
      <div className="about-div-backgroud">
        <div className="about-div-main">
          <h1 className="about-title">Sobre o Code Marathon</h1> 
          <div className="about-div-logo">
            <img alt="Logo do Site" src={process.env.PUBLIC_URL + "/img/logo_white.png"}/>
            <p>O <b>Code Marathon</b> é um sistema onde pessoas 
            interessadas por Maratona de Programação 
            e Olimpíada Brasileira de Informática podem aprender
             os conteúdos recorrentes nessas competições.</p>
          </div>

          <div className="about-div-logo">
            <img alt="Logo do OBI"  src={process.env.PUBLIC_URL + "/img/logo_obi.png"}/>
            <p>A <b>OBI</b> é uma competição organizada nos moldes das outras olimpíadas 
              científicas brasileiras, como Matemática, Física e Astronomia. 
              O objetivo da OBI é despertar nos alunos o interesse por uma ciência 
              importante na formação básica hoje em dia (no caso, ciência da computação), 
              através de uma atividade que envolve desafio, engenhosidade e uma saudável dose
              de competição. A organização da OBI está cargo do Instituto de Computação da UNICAMP.
            </p>
          </div>
          <div className="about-div-logo">
            <img alt="Logo da Maratona"  src={process.env.PUBLIC_URL + "/img/logo_maratona.png"}/>
            <p>A <b>Maratona de Programação</b> é um evento da Sociedade Brasileira de Computação que 
              existe desde o ano de 1996. A Maratona nasceu das competições regionais classificatórias 
              para as finais mundiais do concurso de programação, o International Collegiate Programming
              Contest, e é parte da regional sulamericana do concurso. Ela se destina a alunos e alunas
              de cursos de graduação e início de pós-graduação na área de Computação e afins (Ciência da 
              Computação, Engenharia de Computação, Sistemas de Informação, Matemática, etc). A competição
              promove nos estudantes a criatividade, a capacidade de trabalho em equipe, a busca de novas 
              soluções de software e a habilidade de resolver problemas sob pressão. De ano para ano temos 
              observado que as instituições e principalmente as grandes empresas da área têm valorizado os 
              alunos que participam da Maratona.              
            </p>
          </div>

          <h1 className="about-title">Colaboradores</h1> 
          <Timeline pending="Aguardando sua colaboração...">
            <Timeline.Item color="gray">12/2019 - o site é criado por Paulo Miranda</Timeline.Item>
            {
              collaborators.map((collaborator, key) => <Timeline.Item key={key} color="green"><Collaborator collaborator={collaborator}/></Timeline.Item>)
            }            
          </Timeline>
        </div>
      </div>
    );
  }
} 

export default About;